function getClickedMessage(times) {
    if(times === 1) {
        return `You clicked the button ${times} time`;
    } else {
        return `You clicked the button ${times} times`;
    }
}

let numClicks = 0;
const btn = document.querySelector('button');
btn.addEventListener('click', () => {
    numClicks++;
    btn.innerText = getClickedMessage(numClicks);
});